import React, { Component } from "react";
import axios from "axios";
import { Consumer } from "../../context";

class Search extends Component {
  state = {
    trackTitle: ""
  };

  findTrack = (dispatch, e) => {
    e.preventDefault();
    axios
      .get(
        `http://cors-anywhere.herokuapp.com/http://api.musixmatch.com/ws/1.1/track.search?q_track=${
          this.state.trackTitle
        }&page_size=10&page=1&s_track_rating=desc&apikey=${
          process.env.REACT_APP_MM_KEY
        }`
      )
      .then(res => {
        //Reset the global state(comming from context API)
        dispatch({
          type: "SEARCH_TRACKS",
          payload: res.data.message.body.track_list
        });

        //Clear search input
        this.setState({ trackTitle: "" });
      })
      .catch(err => console.log(err));
  };

  onChange = e => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };

  render() {
    return (
      <div>
        <Consumer>
          {value => {
            const { dispatch } = value;
            return (
              <div className="card card-body mb-4 p-4">
                <h1 className="display-4 text-center">
                  <i className="fas fa-music" /> Search For A Song
                </h1>
                <p className="text-center lead">Get the lyrics for any song</p>
                <form onSubmit={this.findTrack.bind(this, dispatch)}>
                  <div className="form-group">
                    <input
                      type="text"
                      className="form-control form-control-lg"
                      name="trackTitle"
                      placeholder="Song title .."
                      value={this.state.trackTitle}
                      onChange={this.onChange}
                    />
                  </div>
                  <button className="btn btn-primary btn-lg btn-block mb-3">
                    Get the song lyrics
                  </button>
                </form>
              </div>
            );
          }}
        </Consumer>
      </div>
    );
  }
}

export default Search;



